import { NextRequest, NextResponse } from 'next/server';
import { bundle } from '@remotion/bundler';
import { renderMedia, selectComposition } from '@remotion/renderer';
import path from 'path';
import fs from 'fs';
import { pipeline } from 'stream/promises';
import os from 'os';

export async function POST(req: NextRequest) {
  try {
    const formData = await req.formData();
    const file = formData.get('file') as File;
    const inputPropsStr = formData.get('inputProps') as string;

    if (!file || !inputPropsStr) {
      return NextResponse.json({ error: 'Dosya veya ayarlar eksik' }, { status: 400 });
    }

    // 1. Dosyayı geçici klasöre kaydet
    const buffer = Buffer.from(await file.arrayBuffer());
    const tempDir = os.tmpdir();
    const tempFilePath = path.join(tempDir, `upload-${Date.now()}-${file.name}`);
    await fs.promises.writeFile(tempFilePath, buffer);

    // 2. Input Props'u güncelle (Video yolunu sunucudaki dosya yolu yap)
    const inputProps = JSON.parse(inputPropsStr);
    inputProps.videoSrc = tempFilePath; // Remotion yerel dosya yolunu okuyabilir

    // 3. Projeyi Bundle Et (Derle)
    // Not: src/remotion/index.ts dosyasının var olduğu varsayılır
    const entryPoint = path.join(process.cwd(), 'src', 'remotion', 'index.ts');
    const bundleLocation = await bundle({
      entryPoint,
      // Webpack yapılandırması gerekirse buraya eklenebilir
    });

    // 4. Kompozisyonu Seç
    const composition = await selectComposition({
      serveUrl: bundleLocation,
      id: 'KidsAIReel', // Remotion Root içindeki ID ile aynı olmalı
      inputProps,
    });

    // 5. Render İşlemi (MP4 Oluştur)
    const outputLocation = path.join(tempDir, `render-${Date.now()}.mp4`);
    await renderMedia({
      composition,
      serveUrl: bundleLocation,
      codec: 'h264',
      outputLocation,
      inputProps,
    });

    // 6. Dosyayı İstemciye Gönder
    const videoBuffer = await fs.promises.readFile(outputLocation);
    
    // Temizlik (İsteğe bağlı, hemen silmek bazen stream hatası verebilir)
    // await fs.promises.unlink(tempFilePath);
    // await fs.promises.unlink(outputLocation);

    return new NextResponse(videoBuffer, {
      headers: {
        'Content-Type': 'video/mp4',
        'Content-Disposition': 'attachment; filename="render.mp4"',
      },
    });

  } catch (error) {
    console.error('Render Hatası:', error);
    return NextResponse.json(
      { error: 'Render işlemi başarısız oldu', details: String(error) }, 
      { status: 500 }
    );
  }
}
